$(function () {
    yadcf.initDefaults({
        language: {
            select: Lang.get('general.select_value'),
            select_multi: Lang.get('core.select_values'),
            filter: Lang.get('general.type_to_filter'),
            range: [Lang.get('general.from'), Lang.get('general.to')],
            date: Lang.get('core.select_a_date')
        },
    });

    $.extend(true, $.fn.dataTable.defaults, {
        "autoWidth": false,
        "pagingType": "input",
        "bProcessing": true,
        "oClasses": "sp-table-grid sp-dataTable",
        "sDom": "frtip",
        "responsive": {
            details: {
                renderer: function (api, rowIdx, columns) {
                    var data = $.map(columns, function (col, i) {
                        if (!col.hidden) {
                            return '';
                        }

                        // BC - we've declared our own renderer function because we don't want to display the
                        // transparent ticket grid column icons. This code is how it used to work in 1.0.7 before
                        // upgrading to 2.2.3.
                        var header = $(api.column(col.columnIndex).header());

                        return '<li data-dtr-index="'+col.columnIndex+'" data-dt-row="'+col.rowIndex+'" data-dt-column="'+col.columnIndex+'">' +
                            '<span class="dtr-title">' +
                            (header.text() ? header.text() + ':' : '') +
                            '</span> ' +
                            '<span class="dtr-data">' +
                            col.data +
                            '</span>' +
                            '</li>';
                    }).join('');

                    return data ? $('<ul data-dtr-index="'+rowIdx+'" class="dtr-details"/>').append(data) : false;
                }
            }
        },
        "language": {
            "sEmptyTable":     Lang.get('core.sEmptyTable'),
            "sInfo":           Lang.get('core.sInfo'),
            "sInfoEmpty":      Lang.get('core.sInfoEmpty'),
            "sInfoFiltered":   Lang.get('core.sInfoFiltered'),
            "sInfoPostFix":    Lang.get('core.sInfoPostFix'),
            "sInfoThousands":  Lang.get('core.sInfoThousands'),
            "sLengthMenu":     Lang.get('core.sLengthMenu'),
            "sLoadingRecords": Lang.get('core.sLoadingRecords'),
            "sProcessing":     Lang.get('core.sProcessing'),
            "sSearch":         "",
            "searchPlaceholder": Lang.get('core.sSearchPlaceholder'),
            "sZeroRecords":    Lang.get('core.sZeroRecords'),
            "oPaginate": {
                "sFirst":    Lang.get('general.first'),
                "sLast":     Lang.get('general.last'),
                "sNext":     Lang.get('general.next'),
                "sPrevious": Lang.get('general.previous')
            },
            "oAria": {
                "sSortAscending":  Lang.get('core.sSortAscending'),
                "sSortDescending": Lang.get('core.sSortDescending')
            }
        }
    });

    /**
     * Set a global error callback for DataTables. Here we want to gracefully handle
     * any generated errors. These typically occur when the JSON is invalid.
     *
     * See: https://datatables.net/reference/event/error
     *
     * @param settings  DataTables settings object (https://datatables.net/reference/type/DataTables.Settings)
     * @param techNote  Tech note error number - use http://datatables.net/tn/{techNote} to look up a description
     * @param message   Description of error
     */
    $.fn.dataTable.ext.errMode = function (settings, techNote, message) {
        console.error(message);

        // Show error message
        $('#js-message-box').removeClass('sp-alert-success').addClass('sp-alert-error')
            .html(Lang.get('messages.datatable_error'))
            .fadeIn().delay(10000).fadeOut();

        // Remove the "Processing..." message
        $('.dataTables_processing').hide();
    };

    // Default datatables callback events.
    $(document)
        .on('draw.dt', function () {
            timeAgo.render($('time.timeago'));
        })
        .on('page.dt', function (e, settings) {
            var api = new $.fn.dataTable.Api(settings),
                table = api.table(),
                header = table.header(),
                row = table.row(0).node(),
                isElementInViewport = function (el) {
                    var rect = el.getBoundingClientRect(),
                        th = $(header).find('th:visible'),
                        thRect = th.length ? th.get(0).getBoundingClientRect() : {'bottom' : 0};

                    // Check to make sure below a table header (for sticky), no header will assume top of page.
                    return (
                        rect.top >= thRect.bottom &&
                        rect.left >= 0 &&
                        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
                        rect.right <= (window.innerWidth || document.documentElement.clientWidth)
                    );
                };

            // When changing page, scroll to the top of the table if it's currently out of view. Getting the
            // position of the table container plus the table node plus any margin applied to the container gives the
            // exact offset to the top of page.
            if (! isElementInViewport(row)) {
                $('#content').animate({
                    scrollTop: $(table.container()).position().top
                        + $(table.node()).position().top
                        + parseFloat($(table.container()).css('margin-top') || 0)
                }, 300);
            }
        })
        .on('length.dt', function (e, settings, len) {
            $.post(laroute.route('core.set.datatableRows'), {
                count: len
            });
        });
});